/* _c-koh-footer-copyright.scss

   Partial SCSS containing styles for the copyright area in the global footer
*/

.c-koh-footer-copyright {
	@include koh-page-gutters-padding();
	padding-top: 20px;
	padding-bottom: 20px;
	text-align: center;
	background: $koh-color-pdk-grey;
	@media #{$koh-media-screen-md} {
		border-top: 1px solid $koh-color-pmd-grey;
	}
	.koh-copyright-text {
		font-size: 12px;
		color: $koh-color-plt-grey;
	}
}
.c-koh-printfooter {
	display: none;
}

	