/* _c-koh-footer-nav.scss

   Partial SCSS containing styles for the navigation links in the global footer
*/

.c-koh-footer-nav {
    @include koh-page-gutters-padding();
    background: $koh-color-pdk-grey;
    .koh-nav-items {
        @include koh-make-container-row;
        margin: 0;
        .koh-nav-section {
            @media #{$koh-media-nav-breakpoint} {
                display: block;
                float: left;
                width: 20%;
            }
            .koh-nav-section-title {
                display: block;
                position: relative;
                padding: 15px;
                color: $koh-color-slt-grey;
                font-size: 14px;
                text-transform: uppercase;
                border-bottom: 1px solid $koh-color-pmd-grey;
                &:after {
                    display: block;
                    position: absolute;
                    top: 15px;
                    right: 15px;
                    font-family: 'KohlerIcons';
                    content: "\e612";
                }
                &.active:after {
                    content: "\e600";
                }
                a {
                    display: block;
                    color: $koh-color-slt-grey;
                    text-decoration: none;
                }
                &.active + .koh-nav-section-items {
                    display: block;
                }
                @media #{$koh-media-nav-breakpoint} {
                    padding: 0;
                    margin: 48px 0 28px 0;
                    font-size: 12px;
                    border-bottom: none;
                    &:after {
                        content: none;
                    }
                    &.active:after {
                        content: none;
                    }
                }
            }
            .koh-nav-section-items {
                padding: 0;
                display: none;
                @media #{$koh-media-nav-breakpoint} {
                    display: block;
                }
                li {
                    display: block;
                }
                a {
                    display: inline-block;
                    position: relative;
                    padding: 15px 30px;
                    color: $koh-color-plt-grey;
                    font-size: 14px;
                    border-bottom: 1px solid $koh-color-pmd-grey;
                    width: calc(100%);
                    @media #{$koh-media-nav-breakpoint} {
                        padding: 0;
                        font-size: 12px;
                        border-bottom: none;
                        &:hover {
                            color: $koh-color-white;
                            text-decoration: none;
                            .default-icon {
                                background-color: $koh-color-white;
                            }
                        }
                    }
                    .label {
                        @media #{$koh-media-nav-breakpoint} {
                            display: block;
                            margin-bottom: 15px;
                        }
                    }
                    .icon {
                        position: absolute;
                        top: 11px;
                        left: 30px;
                        font-size: 20px;
                        @media #{$koh-media-nav-breakpoint} {
                            top: -5px;
                            left: 0;
                        }
                        & + .label {
                            padding-left: 30px;
                            line-height: 21px;
                        }
                    }
                    .default-icon {
                        background: url(../icons/favicon.png);
                        background-color: $koh-color-plt-grey;
                        background-repeat: no-repeat;
                        background-position: center center;
                        background-size: 60%;
                        width: 20px;
                        height: 20px;
                        top: 16px;
                        border-radius: 3px;
                        &:hover {
                            background-color: $koh-color-white;
                        }
                        @media #{$koh-media-nav-breakpoint} {
                            top: 0;
                        }
                    }
                }
            }
        }
    }
}
