/* Print and share buttons */
.koh-print-share-tools {
  margin: 15px 0 0;
  min-height: 44px;
  overflow: hidden;
}

.koh-print-share-tools .koh-page-share,
.koh-print-share-tools .koh-page-print {
  display: block;
  float: left;
  padding-left: 2px;
}

.koh-print-share-tools .koh-page-share button,
.koh-print-share-tools .koh-page-print button {
  background: #e5e5e5 none repeat scroll 0 0;
  border: 0 none;
  border-radius: 4px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
  color: #232323;
  display: inline-block;
  font-family: HelveticaBold, Helvetica, sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 40px;
  line-height: 1em;
  margin: 0 5px 0 0;
  outline: 0 none;
  padding: 0;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  width: 40px;
}

.koh-print-share-tools .koh-page-share button .label,
.koh-print-share-tools .koh-page-print button .label {
  display: none;
}

.koh-page-share-popover {
  background: #fff none repeat scroll 0 0;
  color: #232323;
  display: none;
  list-style: outside none none;
  margin-bottom: 0;
  min-width: 238px;
  padding: 15px;
}

.koh-page-share-popover button {
  background: #fff none repeat scroll 0 0;
  border: 0 none;
  color: #232323;
  display: block;
  font-family: HelveticaRoman, Helvetica, sans-serif;
  font-size: 1.4em;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
  outline: 0 none;
  padding: 5px;
  text-align: left;
  width: 100%;
}

.koh-page-share-popover button .icon {
  font-size: 20px;
  height: 20px;
  margin-right: 10px;
  vertical-align: middle;
  width: 20px;
}

.koh-page-share-popover button.koh-share-facebook .icon {
  color: #325a90;
}

.koh-page-share-popover button.koh-share-twitter .icon {
  color: #55acee;
}

.koh-page-share-popover button:hover {
  background: #eee none repeat scroll 0 0;
}

