#iv-container {
  position: fixed;
  background: #0d0d0d;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
  z-index: 1000;
}

.iv-container {
  overflow: hidden;
}

.iv-close {
  width: 32px;
  height: 32px;
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  text-shadow: 0 0 3px #6d6d6d;
  transition: all ease .2s;
}

.iv-close:after,
.iv-close:before {
  content: "";
  height: 4px;
  width: 32px;
  background: #FFF;
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -2px;
}

.iv-close:before {
  transform: rotate(45deg);
}

.iv-close:after {
  transform: rotate(-45deg);
}

.iv-close:hover {
  transform: rotate(90deg);
}

.iv-snap-view {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 20px;
  left: 20px;
  border: 1px solid #666;
  background: #000;
  z-index: 100;
  transition: all ease .4s;
  opacity: 0;
}

.iv-snap-image-wrap {
  display: inline-block;
  position: absolute;
  max-width: 150px;
  max-height: 150px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
}

.iv-snap-image {
  position: relative;
}

.iv-image-view,
.iv-large-image,
.iv-loader,
.iv-snap-handle,
.iv-zoom-handle,
.iv-zoom-slider {
  position: absolute;
}

.iv-snap-handle {
  border: 1px solid #ccc;
  transform: translate3d(0, 0, 0);
  box-shadow: 0 0 0 200px rgba(0, 0, 0, 0.5);
}

.iv-zoom-slider {
  width: 100%;
  border: 1px solid #666;
  border-top: 0;
  background: rgba(204, 204, 204, 0.1);
  height: 15px;
  top: 150px;
  left: -1px;
}

.iv-zoom-handle {
  width: 20px;
  height: 15px;
  background: #ccc;
}

.iv-image-view {
  height: 100%;
