/* _koh-defaults.scss

   Partial SCSS containing some default settings for Kohler website; use sparingly
*/

[data-icon]:after {
    font-family: KohlerIcons;
    /* Using Kohler's custom icon font */
    content: attr(data-icon);
}

.koh-mar-top-5 {
    margin-top: 5px;
}
.koh-mar-bot-5 {
    margin-bottom: 5px;
}
.koh-error {
    margin-top: 1px;
    color: #e50000;
    font-size: 1.2rem;
}

img,
video {
    max-width: 100%;
    height: auto;
}

.koh-pdf-link {
    padding-left: 1.3em;
    background-image: url(../images/Adobe_PDF_file_icon_32x32.png);
    background-size: 1em 1em;
    background-repeat: no-repeat;
}

.koh-file-link {
    padding-left: 1.3em;
    background-image: url(../images/Kohler_Download_icon_18x18.png);
    background-size: 1em 1em;
    background-repeat: no-repeat;
}

// Back to top
.koh-back-top,
.c-koh-product-faceted-search .koh-back-top {
    position: fixed;
    bottom: 60px;
    right: 60px;
    margin: 0;
    width: 56px;
    height: 56px;
    z-index: 100;
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        right: 30px;
    }
    button {
        display: none;
        width: 56px;
        height: 56px;
        padding: 0;
        font-size: 8px;
        font-weight: 700;
        outline: none;
        border: none;
        border-radius: 4px;
        background: $koh-color-smd-grey;
        transition: opacity 1s ease-in-out;
        cursor: pointer;
        &:hover {
            background: $koh-color-sdk-grey;
        }
        span {
            display: block;
            height: 18px;
            margin: 7px 0 5px 0;
            font-size: 18px;
            font-weight: normal;
        }
    }
}

#back-to-top {
    height: 55px;
    text-align: center;
    color: #000;
    float: right;
    margin-right: 60px;
    margin-bottom: 60px;
    filter: alpha(Opacity=0);
    opacity: 0;
    transition: opacity 1s ease-in-out;
    cursor: pointer;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 999;
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        margin-right: 30px;
    }
    button {
        display: inline-block;
        cursor: pointer;
        font-family: HelveticaBold, Helvetica, sans-serif;
        font-weight: 700;
        font-style: normal!important;
        color: #232323;
        font-size: 13px;
        line-height: 40px;
        text-align: center;
        border-radius: 4px;
        -webkit-border-radius: 4px;
        -ms-border-radius: 4px;
        -moz-border-radius: 4px;
        -o-border-radius: 4px;
        border: none;
        padding-left: 10px;
        padding-right: 10px;
        min-width: 40px;
        height: 56px;
        width: 56px;
        position: relative;
        background-color: #dbdbdb;
        outline: none;
    }
    i:before {
        content: "\e613"!important;
        display: inline-block;
        font-family: KohlerIcons;
        speak: none;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        -webkit-font-smoothing: antialiased
    }
    span {
        position: absolute;
        bottom: -10px;
        left: 0;
        right: 0;
        font-size: 8px
    }
}

.koh-fixed-find-store-btn {
    padding: 0 20px;
    border-radius: 999rem;
    font-size: 1.6rem;
    background-color: #3B659C;
    color: #FFF!important;
    fill: #FFF!important;
    position: fixed;
    right: 15px;
    bottom: 10px;
    line-height: 41px;
    z-index: 100;
    .icon {
        &.koh-icon-envelope {
            margin: 12px 6px 0 0;
            background: url(../images/branding-bar-icons.png) 0 -25px no-repeat;
            width: 10px;
            height: 15px;
            float: left;
            line-height: 41px;
        }
    }
    &:visited, &:hover, &:focus, &:active  {
        text-decoration: none;
    }
}

// Help Us Improve This Site

.koh-improve-site {
    @include koh-page-gutters-padding();
    padding-bottom: 25px;
    width: 100%;
    background: #fff;
    text-align: right;
    margin-left: auto;
    margin-right: auto;
    a {
        text-decoration: none;
        color: #000000;
        font-size: 1.2rem;
        margin-right: 40px;
        &:hover {
            text-decoration: underline;
        }
    }
}