$koh-mobile-nav-height: 56px;
$koh-desktop-nav-height: 80px;
body.unscrollable {
    overflow: hidden;
}

.koh-global-navigation {
    .koh-global-navigation-components {
        background: $koh-color-white;
        color: $koh-color-pdk-grey;
        z-index: $koh-zindex-navbar;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        &.scrollable {
            overflow: auto;
            min-height: 100%;
        }
    }
    .koh-global-navigation-spacer {
        display: block;
        height: $koh-mobile-nav-height;
    }
    @media #{$koh-media-nav-breakpoint} {
        .koh-global-navigation-components {
            position: static;
        }
        .koh-global-navigation-spacer {
            display: none;
        }
        &.fixed {
            .koh-global-navigation-components {
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                z-index: 10000;
                box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
                -webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
                -ms-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
                -moz-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
                -o-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
                .c-koh-branding-bar.collapsed {
                    display: none !important;
                }
                #comparePanel {
                    box-shadow: none;
                    -webkit-box-shadow: none;
                    -ms-box-shadow: none;
                    -moz-box-shadow: none;
                    -o-box-shadow: none;
                }
            }
            .koh-global-navigation-spacer {
                display: block;
                height: $koh-desktop-nav-height;
            }
        }
    }
    #comparePanel {
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
        -webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
        -ms-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
        -moz-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
        -o-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .3);
        position: relative;
        z-index: $koh-zindex-carousel;
        @media #{$koh-media-screen-xs-max} {
            display:none;
        }
    }
}
