    .koh-required-products-panel {
        max-width: 816px;
        padding: 42px 20px;
        text-align: left;
        @media #{$koh-media-screen-sm} {
            padding: 50px;
        }
        .koh-required-products-header {
            display: block;
            padding-bottom: 15px;
            @include koh-font-light( $font-size: 20px);
            text-transform: uppercase;
            border-bottom: solid 1px $koh-color-slt-grey;
            @media #{$koh-media-screen-sm} {
                font-size: 25px;
                text-align: center;
            }
        }
        .koh-product-required-product {
            position: relative;
            overflow: auto;
            padding: 30px 0;
            border-bottom: solid 1px $koh-color-slt-grey;
            .koh-product-summary {
                @include koh-make-column( $xs: 8, $sm: 8);
                position: static;
                float: right;
                padding-left: 20px !important;
                @include koh-font-light( $font-size: 12px);
                color: $koh-color-pmd-grey;
                @media #{$koh-media-screen-sm} {
                    float: left;
                    position: relative;
                    padding-left: 136px !important;
                    padding-right: 20px !important;
                }
                img {
                    @include koh-make-column( $xs: 4, $sm: 2);
                    max-width: 235px;
                    height: auto;
                    position: absolute !important;
                    top: 30px;
                    left: 0;
                    @media #{$koh-media-screen-sm} {
                        width: 100%;
                        top: 0;
                        max-width: 114px;
                    }
                }
                .koh-product-title {
                    display: block;
                    margin-top: 2px;
                    color: $koh-color-pdk-grey;
                    @media #{$koh-media-screen-sm} {
                        font-size: 16px;
                        line-height: 18px;
                    }
                }
                .koh-product-features {
                    margin: 8px 0 0 16px;
                    padding: 0;
                    display: none;
                    @media #{$koh-media-screen-sm} {
                        display: block;
                    }
                    li {
                        margin-top: 13px;
                    }
                }
            }
        }
    }

    .koh-product-popover {
        &.v-koh-bookmark {
            display: none;
            background: $koh-color-white;
            color: $koh-color-pdk-grey;
            min-width: 300px;
            padding: 20px;
            text-align: left;
            .koh-caption {
                @include koh-font( $font-size: 14px, $font-family: $koh-font-sans-serif-bold);
                padding-bottom: 10px;
                border-bottom: 1px solid $koh-color-smd-grey;
                margin-bottom: 10px;
            }
            >form {
                .koh-fieldset-folders {
                    padding: 0;
                    list-style: none;
                    @include koh-font( $font-size: 12px, $font-family: $koh-font-sans-serif-bold);
                    margin-bottom: 10px;
                }
                .koh-form-field {
                    @include koh-font( $font-size: 12px);
                    margin-bottom: 10px;
                    position: relative;
                    .koh-character-count {
                        position: absolute;
                        right: 0;
                    }
                    textarea {
                        width: 100%;
                    }
                }
                button {
                    &.koh-button-add {
                        @include koh-button-blue($padding: 8px 14px);
                    }
                    &.koh-button-cancel {
                        @include koh-button-grey($padding: 8px 14px);
                    }
                }
            }
        }
        &.v-koh-share {
            display: none;
            background: $koh-color-white;
            color: $koh-color-pdk-grey;
            min-width: 238px;
            padding: 15px 20px 15px 20px;
            margin-bottom: 0;
            list-style: none;
            button {
                @include koh-unbutton();
                display: block;
                background: $koh-color-white;
                color: $koh-color-pdk-grey;
                width: 100%;
                padding: 5px;
                @include koh-font( $font-size: 14px);
                line-height: 20px;
                .icon {
                    width: 20px;
                    height: 20px;
                    font-size: 20px;
                    vertical-align: middle;
                    margin-right: 10px;
                }
                &:hover {
                    background: #eee;
                }
                &.koh-share-pinterest .icon {
                    color: #c62b2e;
                }
                &.koh-share-houzz .icon {
                    color: #76b74b;
                }
                &.koh-share-facebook .icon {
                    color: #325a90;
                }
                &.koh-share-twitter .icon {
                    color: #55acee;
                }
                &.koh-share-google .icon {
                    color: #cc4e3f;
                }
                &.koh-share-email .icon {
                    background-color: #3b659c;
                    position: absolute;
                    border-radius: 3px;
                    &:after {
                        content: '';
                        background: url(../images/branding-bar-icons.png) 0 8px no-repeat;
                        background-position: -19px -19px;
                        width: 20px;
                        height: 20px;
                        position: absolute;
                        border-radius: 3px;
                        transform: scale(.8);     
                    }
                    
                }
                &.koh-share-email .label {
                    margin-left: 30px;
                }
                &.koh-share-showroom .icon {
                    color: #000000;
                }
            }
        }
    }

    .koh-share-email-remodal.remodal {
        position: relative;
        padding: 50px 2.5%;
        font-size: 14px;
        max-width: 600px;
        .remodal-close {
            position: absolute;
            top: 30px;
            right: 27px;
            z-index: 10001;
        }
        .modal-title {
            display: block;
            width: 100%;
            font-family: HelveticaLight, Helvetica, sans-serif;
            font-weight: 200;
            font-style: normal !important;
            font-size: 1.75em;
            text-transform: uppercase;
            margin-top: 0;
            margin-bottom: 30px;
            padding-bottom: 15px;
            border-bottom: solid 1px #ccc;
        }
        .koh-product-share-description {
            font-family: HelveticaBold, Helvetica, sans-serif;
            font-size: 1.1em;
            font-weight: 400;
            line-height: 1em;
            color: #000;
            margin-bottom: 20px;
            span {
                font-family: HelveticaRoman, Helvetica, sans-serif;
            }
        }
        .koh-success-message {
            color: #436D07;
            text-align: center !important;
            font-size: 1.2em;
        }
        .koh-failure-message {
            color: #e50000;
            text-align: center !important;
            font-size: 1.2em;
            padding-bottom: 20px;
            span {
                font-weight: bold;
            }
        }
        .koh-share-email-form {
            text-align: left;
            .koh-share-note-head {
                font-size: 11px;
                margin-top: 20px;
                span.koh-req-head {
                    margin-left: 20px;
                    color: #bebebe;
                    float: right;
                    margin-bottom: 5px;
                }
                a.koh-privacy-policy {
                    color: #3b659c;
                    float: right;
                }
            }
            [class*=col-] {
                padding-left: 10px;
                padding-right: 10px;
            }
            .form-group {
                label {
                    font-family: HelveticaRoman, Helvetica, sans-serif;
                }
                input {
                    height: 40px;
                }
                textarea {
                    max-width: 100%;                            
                }
                .form-instructions {
                    display: block;
                    color: #bebebe;
                    margin-bottom: 0;
                    font-size: 11px;
                }
                .g-recaptcha {
                    transform-origin: left top;
                    -webkit-transform-origin: left top;
                    display: inline-block;
                    height: 78px;
                    background: url(../images/assets/loader.gif) center center no-repeat;
                    width: 100%;
                }
                .koh-primary-btn,
                .koh-cancel-btn {
                    display: inline-block;
                    height: 40px;
                    cursor: pointer;
                    font-family: HelveticaBold, Helvetica, sans-serif;
                    font-weight: 700;
                    font-style: normal !important;
                    color: #232323;
                    background-color: #dbdbdb;
                    font-size: 13px;
                    line-height: 40px;
                    text-align: center;
                    border-radius: 4px;
                    -webkit-border-radius: 4px;
                    -ms-border-radius: 4px;
                    -moz-border-radius: 4px;
                    -o-border-radius: 4px;
                    border: none;
                    padding: 0;
                    margin: 0;
                    &:active,
                    &:focus {
                        outline: 0;
                        border: none;
                        box-shadow: none;
                    }
                }
                .koh-primary-btn:hover {
                    background-color: #bebebe;
                }
                .koh-cancel-btn {
                    background-color: transparent;
                }
            }
        }
    }